import React from 'react';
// import './style.css'

const Map = () => {

  return <div style={{ width: '85%', height: 'auto', margin: 'auto', paddingBottom: '5%' }}>
    <section className="text-center bygga-parent equip-parent py-5">
      <span className="back-border bygga-text-heading fs-4 fw-bold text-muted ">
        Feel Free to contact with us
      </span>
      <p>
        for any kind of query ask freely.
        our contact information is below

      </p>
    </section>

    <div style={{ width: '100%', margin: 'auto', display: 'flex', border: '12px solid gainsboro' }}>
      <div className="mapouter"  >

        <div className=" py-1 p-4 " >
          {/* <Form.Group > */}
          <div className="searchs d-flex align-items-center" >
            <i className="fa fa-map-marker-alt  text-danger mx-2 fa-2x"></i>
            <input className="search-inputs " disabled value="Beep" aria-label="Search" />

          </div>
          <div className="searchs d-flex align-items-center" >
            <i className="fa fa-envelope text-danger mx-2 fa-2x"></i>
            <input className="search-inputs " disabled value="beepstore@gmail.com" aria-label="Search" />

          </div>
          <div className="searchs d-flex align-items-center" >
            <i className="fa fa-phone-square-alt text-danger mx-2 fa-2x"></i>
            <input className="search-inputs " disabled value="+923007676183" aria-label="Search" />

          </div>


          <div className="d-flex align-items-center justify-content-center" style={{ textAlign: 'center', display: 'flex' }}>
            <img className="imgs"
                 src="/images/logo1.jpg"></img>
            <h4 style={{ fontFamily: 'bold', fontSize: '2rem', color: 'red' }}>Beep</h4>
          </div>


          <div style={{ padding: '1%' }}>
            <div className=" d-flex align-items-center justify-content-center">
              <i className="fa  fa-2x mx-3 fa-instagram" aria-hidden="true"></i>
              <i className="fa fa-2x  mx-3 fa-twitter" aria-hidden="true"></i>
              <i className="fa fa-2x mx-3 fa-facebook" aria-hidden="true"></i>
            </div>

          </div>
        </div>

      </div>
      <div className="mapouter" >
        <div className="gmap_canvas">
          <iframe title="Map" className="gmap_iframe" width="100%" frameborder="0" scrolling="no" marginheight="0" marginwidth="0"
            src="https://goo.gl/maps/nNv95uRNeMEPLWYK6">

          </iframe>
          <a href="https://goo.gl/maps/nNv95uRNeMEPLWYK6">

           

          </a>
        </div>
      </div>
    </div>
  </div>
}


export default Map;