import React,{useState} from "react";
import { Link } from "react-router-dom";
import CartScreen from "../screens/CartScreen";
import { logout } from "../actions/UserAction";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { useHistory } from 'react-router-dom'

function Header() {
  const [keyword, setKeyword] = useState('')
  let history = useHistory()

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const dispatch = useDispatch();

  const logoutHandler = () => {
    dispatch(logout());
  };
  const submitHandler = (e) => {
    console.log("okk");
    e.preventDefault()
    if (keyword) {
        history.push(`/?keyword=${keyword}&page=1`)
    } else {
        history.push(history.push(history.location.pathname))
    }
}

  return (
    <div>
      <header className="header_section">
        <div className="header_top">
          <div className="container-fluid">
            <div className="top_nav_container">
              <div className="contact_nav">
                <a href>
                  <i className="fa fa-phone" aria-hidden="true" />
                  <span className="text-white" >Call : +923007676183</span>
                </a>
                <a href>
                  <i className="fa fa-envelope" aria-hidden="true" />
                  <span className="text-white">Email : beepstore@gmail.com</span>
                </a>
              </div>
              <form className="search_form" onSubmit={submitHandler}>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search here..."
                  onChange={(e)=>setKeyword(e.target.value)}
                />
                <button type="submit">
                  <i className="fa fa-search" aria-hidden="true" />
                </button>
              </form>
              <div className="user_option_box">
                {/* <a href className="account-link">
                  <i className="fa fa-user" aria-hidden="true" />
                  <span>My Account</span>
                </a> */}
                <Link to="/cart" className="cart-link">
                  <i className="fa fa-shopping-cart" aria-hidden="true" />
                  <span>Cart</span>
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="header_bottom">
          <div className="container-fluid">
            <nav className="navbar navbar-expand-lg custom_nav-container ">
              <Link className="navbar-brand" to="/">
                <span>
                  <img src="/static/logo1.jpg" alt="My logo" />
                </span>
              </Link>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className> </span>
              </button>
              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav ">
                  <li className="nav-item active">
                    <Link className="nav-link" to="/">
                      Home <span className="sr-only">(current)</span>
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="/products">
                      Products
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="/contactus">
                      Contact Us
                    </Link>
                  </li>
                  {userInfo ? (
                    <li className="nav-item">
                      <span className="nav-link" onClick={logoutHandler}>
                        {" "}
                        Logout
                      </span>
                    </li>
                  ) : (
                    <li className="nav-item">
                      <Link className="nav-link" to="/login">Login</Link>
                    </li>
                  )}

                  <li className="nav-item">
                    <Link className="nav-link" to="/signup">
                      Signup
                    </Link>
                  </li>
                </ul>
              </div>
            </nav>
          </div>
        </div>
      </header>
    </div>
  );
}

export default Header;
