import React from "react";
import { Link } from "react-router-dom";
import Rating from "../components/Rating";
import { useDispatch, useSelector } from "react-redux";
import { useState, useEffect } from "react";
import { listcategoryproducts, listProducts } from "../actions/ProductAction";

function ViewMore({history}) {
  const dispatch = useDispatch();
  const productcategory = useSelector((state) => state.productcategory);
  const { error, loading, productsCategory, page, pages } = productcategory;

  let keyword = history.location.search

  useEffect(() => {
    dispatch(listcategoryproducts("clothes"))
  
  }, [dispatch]);
  return (
    <div>
      <section className="product_section layout_padding">
        <div className="container">
          <div className="heading_container heading_center">
            <h2>Our Products</h2>
          </div>
          <div className="row">
            {productsCategory &&
              productsCategory.map((v, i) => (
                <div className="col-sm-6 col-lg-4">
                  <div className="box">
                    <div className="img-box">
                      <img src={v.image} alt="" />
                      <Link to={`/product/${v.id}`} className="add_cart_btn">
                        <span>Details</span>
                      </Link>
                    </div>
                    <div className="detail-box">
                      <h5>{v.name}</h5>
                      <div className="product_info">
                        <h5>
                          <span>PKR</span> {v.price}
                        </h5>
                        <Rating
                          value={v.rating}
                          text={`${v.num_reviews} reviews`}
                          color={"#f8e825"}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              ))}
          </div>

          
        </div>
      </section>
    </div>
  );
}

export default ViewMore;
